import { deleteForAll, undoDeleteAll } from "../api/api";
export default {
  name: "dustbin",
  data: function data() {
    return {
      chart: ""
    };
  },
  mounted: function mounted() {
    this.init();
  },
  methods: {
    init: function init() {
      if (localStorage.getItem('chart')) {
        this.chart = localStorage.getItem('chart');
      } else {
        this.chart = '图标';
      }
    },
    changeChart: function changeChart(value) {
      this.chart = value;
      localStorage.setItem('chart', value);
    },
    deleteAll: function deleteAll() {
      var _this = this;

      deleteForAll().then(function (res) {
        if (res.status === 200) {
          _this.$refs.child.init();

          _this.$message({
            message: '清空回收站成功',
            type: 'info'
          });
        }
      }).catch(function (e) {
        return _this.$message({
          message: e.response.data,
          type: 'error'
        });
      });
      this.$refs.child.init();
    },
    recoverAll: function recoverAll() {
      var _this2 = this;

      undoDeleteAll().then(function (res) {
        if (res.status === 200) {
          _this2.$refs.child.init();

          _this2.$message({
            message: '全部恢复成功',
            type: 'info'
          });
        }
      }).catch(function (e) {
        return _this2.$message({
          message: e.response.data,
          type: 'error'
        });
      });
    }
  }
};